}
.news__blog h3 {
  font-size: 20px;
  line-height: 40px;
}
.news__blog h4 {
  font-size: 18px;
  line-height: 48px;
}
.news__blog h5 {
  font-size: 16px;
  line-height: 48px;
}
.news__blog h6 {
  font-size: 14px;
  line-height: 48px;
}
.news__blog p, .news__blog li {
  font-family: "Popins-reg", sans-serif;
  font-size: 15px;
  line-height: 30px;
  margin: 0 0 20px 0;
  color: #111111;
}
.news__blog ol {
  list-style-type: none;
  margin: 0 0 30px 0;
  counter-reset: counter-ol;
}
.news__blog ol li {
  display: flex;
  align-items: flex-start;
}
.news__blog ol li::before {
  counter-increment: counter-ol;
  content: counter(counter-ol) " ";
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px 10px 0 0;
  font-family: "Popins-semb", sans-serif;
  color: #FFFFFF;
  min-width: 24px;
  min-height: 24px;
  font-size: 15px;
  line-height: 25px;
  border-radius: 50%;
  background: linear-gradient(80.12deg, #852134 0%, #420D1D 104.17%);
}
.news__blog ol li:not(:last-child) {
  margin: 0 0 20px 0;
}
.news__blog ol li p {
  margin: 0;
  color: #111111;
}
.news__blog ul {
  margin: 0 0 30px 0;
}
.news__blog ul li {
  position: relative;
  padding: 0 0 0 47px;
}
.news__blog ul li::before {
  content: "i";
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 5px;
  left: 0;
  width: 34px;
  height: 34px;
  font-size: 25px;
  line-height: 30px;
  color: #FFFFFF;
  font-family: sans-serif;
  border-radius: 8px;
  background: linear-gradient(252.32deg, #8A2336 0%, #631728 28.99%, #340918 100%);
}
.news__blog ul li:not(:last-child) {
  margin: 0 0 15px 0;
}
.news__blog ul li p {
  margin: 0;
  color: #111111;
}
.news__blog blockquote {
  position: relative;
  padding: 85px 30px 20px;
  border-radius: 8px;
  background: linear-gradient(252.32deg, #8A2336 0%, #631728 28.99%, #340918 100%);
  color: #FFFFFF;
  font-size: 15px;
  line-height: 30px;
  letter-spacing: 0.01em;
  margin: 0 0 20px 0;
}
.news__blog blockquote::before {
  pointer-events: none;
  content: "‟";
  font-size: 65px;
  line-height: 65px;
  transform: scale(1, -1);
  height: 18px;
  color: #E1712C;
  position: absolute;
  display: inline-block;
  top: 30px;
  left: 30px;
}
.news__blog blockquote::after {
  pointer-events: none;
  content: "‟";
  font-size: 65px;
  line-height: 65px;
  transform: scale(-1, 1);
  height: 18px;
  color: #E1712C;
  position: absolute;
  display: inline-block;
  top: 40px;
  left: 52px;
}

